<template>
  <v-list>
    <v-list-item>
      <v-list-item-content>
        <v-list-item-title class="title">
          Information
        </v-list-item-title>
        <v-list-item-subtitle>
          Useful track data
        </v-list-item-subtitle>
      </v-list-item-content>
      <v-list-item-action>
        <v-btn
          icon
          @click="open = false"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-list-item-action>
    </v-list-item>

    <v-divider />

    <v-expansion-panels
      accordion
      multiple
      class="mb-6"
      v-model="panel"
    >
      <v-expansion-panel
        v-for="(group, key) in metrics"
        :key="key"
        style="background: transparent"
      >
        <v-expansion-panel-header expand-icon="mdi-menu-down">
          <span class="text-capitalize">{{ key }}</span>
        </v-expansion-panel-header>

        <v-expansion-panel-content>
          <v-list
            subheader
            two-line
          >
            <v-list-item
              v-for="(metric, index) in group"
              :key="index"
            >
              <v-list-item-content>
                <v-list-item-title v-text="pretty(metric)" />
                <v-list-item-subtitle v-text="metric.name" />
              </v-list-item-content>
              <v-list-item-action>
                <v-btn
                  icon
                  @click="copy(metric)"
                >
                  <v-icon small>mdi-clipboard-text-play</v-icon>
                </v-btn>
              </v-list-item-action>
            </v-list-item>
          </v-list>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </v-list>
</template>

<script>
import { selected as track } from '@/use/tracks'
import { durations, headers } from '@/use/player'
import { clipboard } from '@/use/editor'
import { right as open, mini } from '@/use/drawer'

import { get, set } from '@vueuse/core'

export default {
  data: () => ({
    panel: [1, 1, 0]
  }),

  computed: {
    track: () => get(track),
    headers: () => get(headers),

    open: {
      get: () => get(open),
      set: (value) => set(open, value)
    },

    metrics () {
      return {
        general: [
          {
            name: 'Created',
            filter: 'when',
            value: this.track.created
          },
          {
            name: 'Updated',
            filter: 'when',
            value: this.track.updated
          },
          {
            name: 'ID',
            value: this.track.id
          },
        ],
        timing: [
          {
            name: 'Meter',
            header: 'meter',
            filter: 'meter'
          },
          {
            name: 'Tempo',
            header: 'tempo',
            filter: ''
          }
        ],
        units: [
          {
            name: 'beat unit',
            header: 'beat-unit',
            filter: 'fractionize'
          },
          {
            name: 'pulse beat',
            header: 'pulse-beat',
            filter: 'fractionize'
          },
          {
            name: 'beat units in bar',
            header: 'beat-units-per-measure',
            filter: ''
          },
          {
            name: 'pulse beats in bar',
            header: 'pulse-beats-per-measure',
            filter: ''
          },
          {
            name: 'ms per beat unit',
            header: 'ms-per-beat-unit',
            filter: 'numeric'
          },
          {
            name: 'sec. per beat unit',
            value: get(durations).cast(get(durations).time.beat, { is: 'ms', as: 'second' }),
            filter: 'numeric'
          },
          {
            name: 'ms per pulse beat',
            header: 'ms-per-pulse-beat',
            filter: 'numeric'
          },
          {
            name: 'sec. per pulse beat',
            value: get(durations).cast(get(durations).time.pulse, { is: 'ms', as: 'second' }),
            filter: 'numeric'
          },
          {
            name: 'ms per bar',
            value: get(durations).cast(get(durations).bar.pulse, { as: 'ms' }),
            filter: 'numeric'
          },
          {
            name: 'seconds per bar',
            value: get(durations).cast(get(durations).bar.beat, { is: 'beat', as: 'second' }),
            filter: 'numeric'
          }
        ],
        totals: [
          {
            name: 'total bars',
            // TODO: Update name of this header in `bach`, poorly named (or create alias such as `total-measures`
            header: 'total-beats',
            filter: 'fractionize'
          },
          {
            name: 'total beat units',
            header: 'total-beat-units',
            filter: ''
          },
          {
            name: 'total pulse beats',
            header: 'total-pulse-beats',
            filter: ''
          },
          {
            name: 'total ms',
            value: get(durations).cast(get(durations).total, { as: 'ms' }),
            filter: 'numeric'
          },
          {
            name: 'total seconds',
            value: get(durations).cast(get(durations).total, { as: 'second' }),
            filter: 'numeric'
          }

        ]
      }
    }
  },

  methods: {
    valueOf (metric) {
      return metric.value || this.headers[metric.header]
    },

    pretty (metric) {
      const value = this.valueOf(metric)

      if (metric.filter) {
        return this.$options.filters[metric.filter](value)
      }

      return value
    },

    copy (metric) {
      if (clipboard.isSupported) {
        clipboard.copy(this.valueOf(metric))
      }
    },
  }
}
</script>
